@import url('../components/personal-plan.css');
@import url('../components/cart.css');


.header {
  background: #0B0B0B;
}

.main-title {
  .subtitle p {
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 5px;
  }

  .title {
    .name {
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      letter-spacing: 0;
    }
  }
}

.title-main {
  margin-bottom: 30px;
  margin-top: 70px;
  @media (max-width: 767.98px) {
    margin-top: 35px;
  }

  .subtitle {
    p {
      margin-bottom: 3px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }

  h1 {
    color: #0B0B0B;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 767.98px) {
      font-size: 30px;
      font-weight: 600;
      line-height: normal;
    }
  }
}
.cart__description{
  height: auto;
}
.cases {
  background: #F6F6F8;
  padding-top: 110px;

  &__description {
    margin-top: 5px;
  }

  @media (max-width: 767.98px) {
    padding-top: 74px;
  }

  &__category {
    display: flex;
    flex-wrap: wrap;
  }

  &__category-name {
    padding: 8px 16px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    border-radius: 4px;
    margin-bottom: 16px;
    transition: 0.5s;

    a {
      color: #0B0B0B;
      text-align: center;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }

    &:hover {
      background: #7339F5;
      color: #fff;
      cursor: pointer;

      a {
        color: #fff;
      }
    }

  }

  &__category-name.active {
    background: #7339F5;

    a {
      color: #fff;
    }

  }
}

.cases {
  &__top-info {
    border-radius: 10px;
    border: 1px solid #E7E6E6;
    background: #FFF;
    padding: 17px 17px 17px 30px;
    display: flex;
    align-items: center;
    @media (max-width: 767.98px) {
      padding: 33px 14px 17px 14px;
      flex-wrap: wrap;
    }
  }

  &__info-left {
    width: auto;
    flex: 1 1 auto;

    p.title {
      color: #0B0B0B;
      font-size: 32px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      margin-bottom: 27px;
      margin-top: 14px;
      @media (max-width: 767.98px) {
        font-size: 22px;
        margin-top: 0;
      }
    }
  }

  &__info-right {
    width: 360px;
    height: 222px;
    @media (max-width: 767.98px) {
      width: 100%;
      max-width: 360px;
      height: auto;
      margin-top: 24px;
    }

    img {
      border-radius: 10px;
      max-width: 100%;
      height: auto;
    }
  }

  &__info-block {
    display: flex;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    p {
      &:first-child {
        color: #0B0B0B;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        width: 204px;
        @media (max-width: 767.98px) {
          font-size: 14px;
          width: 140px;
          min-width: 140px;
        }
      }

      &:last-child {
        color: #464646;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        @media (max-width: 767.98px) {
          font-size: 14px;
        }
      }

    }
  }
}

.typography {
  padding-bottom: 120px;
  @media (max-width: 767.98px) {
    padding-bottom: 56px;
  }
  h2 {
    margin-top: 110px;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 17px;
    @media (max-width: 1023.98px) {
      margin-top: 80px;
    }
    @media (max-width: 767.98px) {
      margin-top: 56px;
      font-size: 22px;
    }
  }

  p {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    @media (max-width: 767.98px) {
      font-size: 14px;
    }
  }

  ul {
    padding: 0;

    li {

    }
  }
}

.gallery__navigation {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
  @media (max-width: 767.98px) {
    display: none;
  }

  ._prev {
    margin-left: -68px;
    z-index: 1;
    @media (max-width: 1440.98px) {
      margin-left: -20px;
    }
    @media (max-width: 767.98px) {
      margin: 0 15px;
    }

    &:hover {
      cursor: pointer;
    }
  }

  ._next {
    margin-right: -68px;
    z-index: 1;
    @media (max-width: 1440.98px) {
      margin-right: -20px;
    }
    @media (max-width: 767.98px) {
      margin: 0 15px;
    }

    &:hover {
      cursor: pointer;
    }
  }
}

.cases__list {
  padding-top: 115px;
  background: #F6F6F8;
  padding-bottom: 120px;
  @media (max-width: 767.98px) {
    padding-bottom: 56px;
    padding-top: 76px;
  }
}

.clients__slider {
  position: relative;
  margin-top: 27px;
}

.swiper-slide .cart {
  margin: 0 5px;
  @media (max-width: 767.98px) {
    margin: 0;
  }
}

.swiper-pagination {
  display: none;
  justify-content: center;
  margin-top: 32px;
  @media (max-width: 767.98px) {
    display: flex;
  }
}

.swiper-pagination-bullet {
  height: 2px;
  background: #C4C4C4;
  border-radius: 10px;
  width: 15px;
  margin: 0 5px;
}

.swiper-pagination-bullet-active {
  background: #7339F5;
}